import PropTypes from 'prop-types';
import './panel.scss';

/**
 * React  
 * @param {boolean} isOpened -  ,   React ;
 * @param {string} width - ,  ;
 * @param {string} marginTop - ,    ;
 * @param {string} marginLeft - ,    ;
 */

function Panel({ children, isOpened, width, marginTop, marginLeft }) {
  return (
    <div
      style={{ width, marginTop, marginLeft }}
      className={`panel${isOpened ? ' panel_open' : ''}`}>
      {children}
    </div>
  );
}

Panel.propTypes = {
  isOpened: PropTypes.bool,
  width: PropTypes.string,
  marginTop: PropTypes.string,
  marginLeft: PropTypes.string,
};

export default Panel;
