import PropTypes from 'prop-types';
import './accordion-item.scss';
import Button from '../../../shared/ui/button/button';

/**
 * React   
 * @param {string} title - ,  ;
 * @param {string} description - ,  ;
 * @param {string} tooltipText - ,    ;
 * @param {boolean} isInactive -  ,    ;
 * @param {function} onClick - -,     ;
 */

function AccordionItem({
  children,
  title,
  description,
  tooltipText,
  isInactive,
  onClick,
}) {
  return (
    <div className='accordion-item' title={tooltipText}>
      <div className='accordion-item__icon'>{children}</div>
      <div
        className={`accordion-item__text${isInactive ? ' accordion-item__text_inactive' : ''}`}>
        <Button isInactive={isInactive} onClick={onClick}>
          <h3
            className={`accordion-item__title${isInactive ? ' accordion-item__title_inactive' : ''}`}>
            {title}
          </h3>
          <p
            className={`accordion-item__description${isInactive ? ' accordion-item__description_inactive' : ''}`}>
            {description}
          </p>
        </Button>
      </div>
    </div>
  );
}

AccordionItem.propTypes = {
  children: PropTypes.any,
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  tooltipText: PropTypes.string.isRequired,
  isInactive: PropTypes.bool.isRequired,
  onClick: PropTypes.func.isRequired,
};

export default AccordionItem;
