import PropTypes from 'prop-types';
import './chip.scss';
import IndicatorIcon from '../../assets/icons/indicator-icon';

/**
 * React   chip
 * @param {string} text - ,   chip;
 * @param {boolean} isDisabled -  ,    chip;
 * @param {boolean} isActive -  ,     chip;
 * @param {function} setActive - -,      chip.   "true";
 * @param {string} tooltipText - ,     chip;
 * @param {string} indicatorColor - ,    chip;
 * @param {string} textSize - ,    chip. : 'small', 'large';
 */

function Chip({
  text,
  isDisabled,
  isActive,
  setActive,
  tooltipText,
  indicatorColor,
  textSize,
}) {
  return (
    <button
      type='button'
      disabled={isDisabled}
      className={`chip ${isActive ? 'chip_active' : ''} chip_text-size_${textSize}`}
      onClick={() => setActive(true)}
      title={tooltipText}>
      {text}
      {indicatorColor && (
        <div className='chip__indicator'>
          <IndicatorIcon color={indicatorColor} />
        </div>
      )}
    </button>
  );
}

Chip.propTypes = {
  text: PropTypes.string.isRequired,
  isDisabled: PropTypes.bool.isRequired,
  isActive: PropTypes.bool.isRequired,
  setActive: PropTypes.func.isRequired,
  tooltipText: PropTypes.string.isRequired,
  indicatorColor: PropTypes.string,
  textSize: PropTypes.string.isRequired,
};

export default Chip;
