import PropTypes from 'prop-types';
import './button.scss';

/**
 * React  
 * @param {string} text - ,  ;
 * @param {object} style - ,  ;
 * @param {function} onClick - -,     ;
 * @param {boolean} isInactive -  ,    ;
 * @param {string} tooltipText - ,    ;
 * @param {string} hoverStyle - ,      ;
 */

function Button({
  children,
  className,
  text,
  style,
  onClick,
  isInactive,
  tooltipText,
  hoverStyle,
}) {
  const customClassName = `button button_hover_${hoverStyle || 'opacity'}`;

  return (
    <button
      className={`${customClassName}${isInactive ? ' button_inactive' : ''} ${className || ''}`}
      style={style}
      onClick={onClick}
      disabled={isInactive}
      title={tooltipText}>
      <div className='button__icon'>{children}</div>
      {text}
    </button>
  );
}

Button.propTypes = {
  children: PropTypes.any,
  text: PropTypes.string,
  style: PropTypes.object,
  onClick: PropTypes.func,
  isInactive: PropTypes.bool,
  tooltipText: PropTypes.string,
  hoverStyle: PropTypes.string,
};

export default Button;
